@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@200;300;500&display=swap);
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Josefin Sans', sans-serif;
}

.container {
    height: 100vh;
    width: 100vw;
    background-color: #f3f2ef;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.box {
    width: 25vw;
    height: 60vh;
    border-radius: 0.5rem;
    background: #a5bbdd;
    box-shadow: 0 0.2rem 3rem rgba(0, 0, 0, 0.2);
    position: relative;
    overflow: hidden;
    min-width: 20rem;
    min-height: 35rem;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}

.wave {
    opacity: 0.1;
    position: absolute;
    top: 120%;
    left: 50%;
    background: white;
    width: 50rem;
    height: 50rem;
    margin-left: -25rem;
    margin-top: -20rem;
    -webkit-transform-origin: 50% 48%;
            transform-origin: 50% 48%;
    border-radius: 43%;
    -webkit-animation: drift 3000ms infinite linear;
            animation: drift 3000ms infinite linear;
    z-index: -1;
}

.one {
    -webkit-animation: drift 4000ms infinite linear;
            animation: drift 4000ms infinite linear;
    opacity: 0.2;
    z-index: 1!important;
}

.two {
    -webkit-animation: drift 5000ms infinite linear;
            animation: drift 5000ms infinite linear;
    opacity: 0.3;
    z-index: 3!important;
}

.three {
    -webkit-animation: drift 6000ms infinite linear;
            animation: drift 6000ms infinite linear;
    opacity: 0.4;
    z-index: 3!important;
}

.inputData {
    text-align: center;
    margin-top: 0.5rem;
}

.inputField {
    height: 2rem;
    width: 60%;
    outline: none;
    border-radius: 2rem;
    border: 1px solid transparent;
    font-size: 1rem;
    padding: 0.8rem;
}

.head {
    display: grid;
    place-items: center;
    margin-top: 5rem;
}

.wi {
    color: white;
    font-size: 5rem;
    -webkit-animation: scale 2s ease-in-out infinite linear;
            animation: scale 2s ease-in-out infinite linear;
}

.main {
    height: 60%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.location {
    text-align: center;
    font-size: 2.4rem;
    text-transform: capitalize;
    padding: 1rem;
    z-index: 99999;
    /* animation: move 2s ease-in-out infinite alternate-reverse both; */
}

.temp {
    padding-top: 1rem;
    z-index: 99999;
}

.min_max {
    font-size: 1.4rem;
    font-weight: 400;
    padding-top: 0.4rem;
    z-index: 9999;
}

.time {
    z-index: 9999;
    font-size: 1.2rem;
    padding-top: 0.4rem;
    font-weight: 300;
}

.btn {
    outline: none;
    font-size: 1rem;
    width: 4rem;
    height: 2rem;
    border-radius: 1rem;
    margin-left: 0.4rem;
    border: transparent;
    cursor: pointer;
    background-color: white;
    color: black;
    transition: all 0.4s ease;
}

.btn:hover {
    width: 5rem;
}


/* .fa-location-arrow {
    animation: animate 2s ease-in-out infinite alternate-reverse both;
} */

.error {
    text-align: center;
    padding-top: 2rem;
    font-size: 1.6rem;
}

@-webkit-keyframes drift {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes drift {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}


/* @keyframes move {
