        transform: translateY(-10px)
    }
    100% {
        transform: translateY(10px);
    }
} */

@-webkit-keyframes scale {
    0% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes scale {
    0% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}


